import React, { useState } from 'react';
import CompanyLogo from '../fotos/companyLogo.jpg';
import 'bootstrap/dist/css/bootstrap.css';
import 'bootstrap-icons/font/bootstrap-icons.css';



const TopNav = props => {
  const [isNavCollapsed, setIsNavCollapsed] = useState(true);

  const handleNavCollapse = () => setIsNavCollapsed(!isNavCollapsed);

  return (
    <div style={{ 
      backgroundImage: "linear-gradient(to right, #4880EC, #019CAD)",
      backgroundRepeat: 'no-repeat',
      backgroundSize: 'cover',}}>
      <div className="row p-1" style={{
        position: 'fixed',
        top: 0,
        zIndex: 1,
        width: '100%',
        backgroundImage: "linear-gradient(to right, #4880EC, #019CAD)",
        opacity: 0.9,
      }}>
        <div className="col d-flex text-white">
          <i className="bi bi-geo-alt me-1"></i>
          <h5 className="ms-2">Добрич</h5>
        </div>
        <div className="col d-flex text-white">
          <i className="bi bi-telephone-forward"></i>
          <h5 className="ms-2">+359 893747880</h5>
        </div>
        <div className="col d-flex text-white">
          <i className="bi bi-envelope-check"></i>
             <h5 className="ms-2">jordan.zhendov@abv.bg</h5>
        </div>
      </div>
        <br/>
        <br/>
        <br/>
      <nav className="navbar navbar-expand-lg navbar-light rounded">
        <a className="navbar-brand text-danger font-weight-bolder" href="/">
          <div className="ms-5">
              <img src={CompanyLogo} alt="Logo" width="110" height="145" style={{
                borderTopRightRadius: 100,
                borderBottomLeftRadius: 35,
              }}/>
            </div>
        </a>
        <button className="custom-toggler navbar-toggler me-3 ms-2" type="button" data-toggle="collapse" data-target="#navbarsExample09" aria-controls="navbarsExample09" aria-expanded={!isNavCollapsed ? true : false} aria-label="Toggle navigation" onClick={handleNavCollapse}>
          <span className="navbar-toggler-icon"></span>
        </button>

        <div className={`${isNavCollapsed ? 'collapse' : ''} navbar-collapse`} id="navbarsExample09">
          <div className="navbar-nav me-auto ms-5 mb-lg-0">
              <a className="nav-link text-warning  menuTab ms-3" href="/"><h2>Начало</h2></a>
              <a className="nav-link text-light  menuTab ms-5" href="/info"><h2>За нас</h2></a>
              <a className="nav-link text-light menuTab ms-5" href="/services"><h2>Услуги</h2></a>
              <a className="nav-link text-light menuTab ms-5" href="/gallery"><h2>Галерия</h2></a>
              <a className="nav-link text-light menuTab ms-5" href="/results"><h2>Завършени обекти</h2></a>
          </div>
        </div>
      </nav>
    </div>
  );
}

export default TopNav;