import  Axios  from 'axios';
import React, { useState } from 'react';


const ContactComponent = () => {

    const [data, setPersonData] = useState({
        name: "",
        email: "",
        phone: "",
        message: ""
    })

    const url="http://localhost:8080/send"

    function handleContactForm(e){
        const personData={...data}
        personData[e.target.id] = e.target.value
        setPersonData(personData)
        console.log(personData)
    }

    const [responseData, setresponseData] = useState("")

    function submitData(e){
        e.preventDefault();
        Axios.post(url,{
            name: data.name,
            email: data.email,
            phone: data.phone,
            message: data.message
        })
        .then(res => {
            setresponseData(true);
        })
    }

    return (
        <div className="mt-5"> 
            <p className="text-success h4 ms-5">
                   <strong className="border-bottom border-2 border-success">За Контакти</strong>
            </p>
            <div className="form-box">
                <form className="m-5" onSubmit={(e) => submitData(e)}>
                    <div className="row mb-3">
                        <div className="col form-group">
                            <input className="form-control" onChange={(e) => handleContactForm(e)} value={data.name} id="name" type="text" placeholder="Лице за контакт*"  name="Name"/>
                        </div>
                        <div className="col form-group">
                            <input className="form-control" onChange={(e) => handleContactForm(e)} value={data.email} id="email" type="email" placeholder="Имейл*"  name="Email"/>
                        </div>
                        <div className="col form-group ">
                            <input className="form-control" onChange={(e) => handleContactForm(e)} value={data.phone} id="phone" type="number" placeholder="Телефон за връзка*"  name="Phone"/>
                        </div>
                    </div>
                    <div className="form-group">
                        <textarea className="form-control" onChange={(e) => handleContactForm(e)} value={data.message} id="message" rows="4" cols="50" placeholder="Вашето запитване*" name="Message">
                        </textarea>
                    <p className="text-danger">Всички полета с * са задължителни!</p>
                        <p className="text-success text-center h5 mt-3" style={responseData ? {display: ''} : {display: 'none'}}>Запитаването е изпратено успешно</p>
                            <button className="btn btn-warning text-primary mt-3">Изпращане на запитването</button>
                    </div>
                </form>
            </div>
        </div>
    )
}

export default ContactComponent
