import axios from 'axios';
import React, { useState, useEffect } from 'react';

const HomeComponent = () => {
    // loading the currentobjects
    const [currentObjects, setcurrentObjects] = useState([])
    useEffect(() => {
        const fetshCurrent = async () => {
            const respose = await axios('http://localhost:8080/api/currentobjects');
            setcurrentObjects(respose.data);
        }
        fetshCurrent();

    },[]);
    // showing and hiding the additional info
    const [infoBox, setInfoBox] = useState('none');
        const showInfoBox = event => {
            event.preventDefault()
            if(infoBox === 'block'){
                setInfoBox('none');
                return;
            }
            setInfoBox('block');
        }

    const useCurrentObjectts = currentObjects.map((currentobject) =>{
        return <div>
            <div className="row" id={currentobject.id}>
            <div className="col-1"></div>

                <div className="col card text-light mb-5" style={{
                    backgroundImage: "linear-gradient(to right, #4880EC, #019CAD)",
                    borderBottomLeftRadius: 50,
                    borderTopRightRadius: 50,

                }}>
                    <p className="card-text border-bottom text-center h3">{currentobject.name}</p>
                    <img src={currentobject.pictureUrl}  className="card-img-top p-1 m-2" alt="..." style={{
                        borderTopRightRadius: 35,
                        borderTopLeftRadius: 35,
                        borderBottomLeftRadius: 35,
                        borderBottomRightRadius: 35,
                    }}/>
                    <div className="card-body">
                        <p className="card-text h4 text-center">{currentobject.description}</p>
                        <p className="card-text border-top text-center m-4">Начало: 20.01.21г.</p>
                        <p className="card-text border-top text-center m-4">Край: 20.02.21г.</p>
                        <div className="text-center">
                        <button onClick={showInfoBox} className="btn text-success btn-light position-center" >Повече информация</button>
                        </div>
                    </div>
                </div>
                <div className="col" style={{
                    margin: 'auto',
                    display: infoBox
                }}>
                    <div className="card">
                        <ul className="list-group list-group-flush">
                            <li className="list-group-item">Обекта е стартиран</li>
                            <li className="list-group-item">Време за изпълнение</li>
                            <li className="list-group-item">Начало на обекта</li>
                        </ul>
                    </div>
                </div>
                <div className="col-1"></div>
            </div>
        </div>
    })

    return (
        <div className="homeBackPhoto" style={{ 
            backgroundImage: `url("https://as2.ftcdn.net/v2/jpg/03/27/78/93/500_F_327789317_T0sF1wvaxBiUaPvLby6lAksSetQvM7Hf.jpg")`,
            backgroundRepeat: 'repeat',
            }}>
            <br/>
            <br/>
            <br/>
            <h1 className="text-light p-4 text-center" style={{
                        backgroundImage: "linear-gradient(to right, #4880EC, #019CAD)",

            }}><i><strong>Текущи Обекти</strong></i></h1>
            <br/>
            <br/>
            <div>
                {useCurrentObjectts}
            </div>
            <br/>
            <br/>
            <br/>
            <br/>
            <br/>
        </div>
    )
}

export default HomeComponent
