import React from 'react'

const GoalMissionComponent = () => {
    return (
        <div className="goalMission">
            <br/>
            <br/>
            <br/>
                <p className="text-center text-success h4 mb-3">
                    <strong className="border-bottom border-2 border-success">ВИЗИЯ, МИСИЯ, ЦЕЛИ</strong>
                </p>
                <p className="m-5">Нашата визия за бъдещето на Строителна фирма НикСтрой94 е взаимното уважение и професионализъм да продължават да формират основата на успеха й и развитието на обществото и региона.</p>
                <p className="h4 ms-5">Мисията ни е да създадем среда, в която всеки един от нашите клиенти и партньори печели.</p>
                <p className="ms-5">С цел постигането й, непрекъснато се стремим към:</p>
                <ul className="ms-5" >
                    <li className="m-4">Открити отношения с нашите служители, на базата на взаимно доверие и уважение.</li>
                    <li className="m-4">Безкомпромисен ангажимент към качеството на изработената продукция, безопасността на нашите служители и грижата за околната среда.</li>
                    <li className="m-4">Почтеност и прозрачност във всичко, което правим, гарантиращи финансова отговорност към нашите партньори.</li>
                </ul>
                <p></p>
            <br/>
            <br/>
            <br/>
        </div>
    )
}

export default GoalMissionComponent
