import './App.css';
import Footer from './components/Footer';
import Header from './components/Header';
import HomePageComponent from './components/homepage/HomePageComponent';
import InfoComponent from './components/infoCompany/InfoComponent';
import GalleryComponent from './components/gallery/GalleryComponent';
import FinishWorkComponent from './components/doneWork/FinishWorkComponent';
import ServicesComponent from './components/services/ServicesComponent';
import {BrowserRouter, Route, Routes} from 'react-router-dom';


function App() {
  return (
    <div className="App">
      <Header/>
      <BrowserRouter>
        <Routes>
          <Route exact path="/" element={<HomePageComponent/>}></Route>
          <Route exact path="/info" element={<InfoComponent/>}></Route>
          <Route exact path="/services" element={<ServicesComponent/>}></Route>
          <Route exact path="/gallery" element={<GalleryComponent/>}></Route>
          <Route exact path="/results" element={<FinishWorkComponent/>}></Route>
        </Routes>
      </BrowserRouter>
      <Footer/>
    </div>
  );
}

export default App;
